$gold: #e7ba82;
$light-blue: #96cbd2;

.navBarFlexContainer {
    z-index: 2;
    height: 64px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    box-shadow: 0px 3px 4.85px 0.15px rgba(36, 45, 59, 0.1);
    position: sticky;
    top: 0;
    .navLogo {
        padding: 0px 50px 0px 50px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 900;
        font-size: 35px;
        .cultureLogo {
            color: $light-blue;
        }
        .fitLogo { 
            color: $gold;
            text-decoration: underline;
        }
    }
    .navBarInnerContainer {
        //background-color: #4E84A6;
        //border-radius: 50px 0px 0px 50px;
        height: 64px;
        display: flex;
        align-items: center;
        //filter: drop-shadow(5px 2px 1px #00000060);
        .navElement {
            display: inline-block;
            padding: 0px 50px 0px 50px;
            a { 
                text-transform: lowercase;
                font-family: Arial, Helvetica, sans-serif;
                color: grey;
                font-weight: lighter;
                text-decoration: none;
                transition: all .2s ease;
            }
            a:hover {
                text-decoration: underline;
            }
        }
    }
}

